import React, { Suspense } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import { Toaster } from "sonner";
import ScrollReveal from '@/components/ScrollReveal';

// Lazy load pages for better performance
const Home = React.lazy(() => import('@/pages/Home'));
const Login = React.lazy(() => import('@/pages/Login'));
const Signup = React.lazy(() => import('@/pages/Signup'));
const Dashboard = React.lazy(() => import('@/pages/MemberDashboard'));
const Profile = React.lazy(() => import('@/pages/Profile'));
const ApplyContributor = React.lazy(() => import('@/pages/ApplyContributor'));
const Connect = React.lazy(() => import('@/pages/Connect'));
const Contact = React.lazy(() => import('@/pages/Contact'));
const Privacy = React.lazy(() => import('@/pages/Privacy'));
const Terms = React.lazy(() => import('@/pages/Terms'));
const Cookies = React.lazy(() => import('@/pages/Cookies'));
const ForgotPassword = React.lazy(() => import('@/pages/ForgotPassword'));
const ResetPassword = React.lazy(() => import('@/pages/ResetPassword'));
const InvestmentManagement = React.lazy(() => import('@/pages/publishing/journals/investment-management'));

function App() {
  return (
    <Router future={{ v7_startTransition: true, v7_relativeSplatPath: true }}>
      <div className="App">
        <Suspense fallback={
          <div className="min-h-screen flex items-center justify-center">
            <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-newtifi-teal"></div>
          </div>
        }>
          <ScrollReveal>
            <Routes>
              <Route path="/" element={<Home />} />
              <Route path="/login" element={<Login />} />
              <Route path="/signup" element={<Signup />} />
              <Route path="/dashboard" element={<Dashboard />} />
              <Route path="/profile" element={<Profile />} />
              <Route path="/apply-contributor" element={<ApplyContributor />} />
              <Route path="/connect" element={<Connect />} />
              <Route path="/contact" element={<Contact />} />
              <Route path="/privacy" element={<Privacy />} />
              <Route path="/terms" element={<Terms />} />
              <Route path="/cookies" element={<Cookies />} />
              <Route path="/forgot-password" element={<ForgotPassword />} />
              <Route path="/reset-password" element={<ResetPassword />} />
              <Route path="/publishing/journals/investment-management" element={<InvestmentManagement />} />
            </Routes>
          </ScrollReveal>
        </Suspense>
        <Toaster position="top-right" />
      </div>
    </Router>
  );
}

export default App;